:root {
  --doc-height: 100%;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  width: 100%;
  height: 100vh; /* fallback for Js load */
  height: var(--doc-height);
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-content: center;
  height: 100vh; /* fallback for Js load */
  height: var(--doc-height);
}

a {
  text-decoration: underline;
  color: #2D4190;
}
li {
  padding: 0.2rem;
}

p {
  font-family: "Open Sans", sans-serif;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Open Sans", sans-serif;
  color: #2D4190;
}

#logo1-text #logo-heading {
  color: white !important;
}

#logo1-text #logo-heading {
  color: white !important;
}

[class^=Display] {
  font-family: "Open Sans", sans-serif;
}

#welcome #welcome-display-text {
  color: white !important;
}

.section-title {
  color: #2D4190;
  font-weight: 400;
}

.section-link {
  font-size: 0.8rem;
  color: #231F20;
}
.section-link:hover {
  color: #90ccee;
}

.news-tags {
  color: #F2F2F2;
}

.news-title {
  color: #231F20;
}

.news-date {
  color: #424242;
}

.calendar-date {
  color: #F2F2F2;
}

.calendar-month {
  color: #231F20;
}

.calendar-event {
  color: #2D4190;
}

.calendar-time {
  color: #424242;
}

.content-column h1, .content-column h2, .content-column h3, .content-column h4, .content-column h5, .content-column h6,
.content-column .h1, .content-column .h2, .content-column .h3, .content-column .h4, .content-column .h5, .content-column .h6 {
  color: #2D4190;
}

.is-style-wlt-file-block a,
.editor-styles-wrapper .is-style-wlt-file-block a {
  background-color: #e2e6f8;
  padding: 1rem;
  margin-bottom: 4px;
  color: #231F20;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  align-items: center;
}
.is-style-wlt-file-block a:hover,
.editor-styles-wrapper .is-style-wlt-file-block a:hover {
  background-color: #2D4190;
  color: white;
}
.is-style-wlt-file-block a:hover::before,
.editor-styles-wrapper .is-style-wlt-file-block a:hover::before {
  color: white;
}
.is-style-wlt-file-block a::before,
.editor-styles-wrapper .is-style-wlt-file-block a::before {
  font: var(--fa-font-solid);
  content: "\f15b";
  font-size: 1rem;
  color: #424242;
  padding-right: 1rem;
}

.is-style-wlt-quote-block,
.editor-styles-wrapper .is-style-wlt-quote-block {
  margin-top: 1.8rem;
  margin-bottom: 1.8rem;
  text-align: center;
  padding-left: 4rem;
  padding-right: 4rem;
}
.is-style-wlt-quote-block p,
.editor-styles-wrapper .is-style-wlt-quote-block p {
  font-family: "Cormorant", serif;
  font-weight: 300 !important;
}

.custom #my-search {
  float: right;
  width: 306px;
}

.custom form.search_form_visible {
  width: 306px;
}

.custom form.search_form_visible #s {
  background: #fff;
  border: 1px solid #000;
  font-size: 12px;
  margin: 0 6px 0 0;
  padding: 4px;
  width: 215px;
  float: left;
}

.custom #my-search input#searchsubmit.my-search {
  background: #000;
  color: #fff;
  cursor: pointer;
  float: right;
  padding: 4px;
}

.wlt-latest-posts img {
  margin-bottom: 0.5rem;
}
.wlt-latest-posts li {
  border: #F5F5F5 1rem solid;
  box-shadow: 5px 10px 18px #888888;
  padding: 0;
  min-height: fit-content;
}
.wlt-latest-posts a.wp-block-latest-posts__post-title {
  color: #5B2A84;
  font-weight: bold;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}
.wlt-latest-posts a.wp-block-latest-posts__post-title:visited {
  color: #5B2A84;
}
.wlt-latest-posts a.wp-block-latest-posts__post-title, .wlt-latest-posts time, .wlt-latest-posts div.wp-block-latest-posts__post-excerpt {
  padding-left: 0.8rem;
  padding-right: 0.8rem;
}

.is-style-wlt-custom-button a,
.editor-styles-wrapper .is-style-wlt-custom-button a {
  background-color: #2D4190 !important;
  padding: 0.6rem 1.2rem 0.6rem 1.2rem !important;
  font-weight: 500 !important;
  font-size: 1rem;
  color: white !important;
  border-radius: 0.4rem !important;
  min-width: 3rem;
  border: #2D4190 solid 1px !important;
  margin-top: 1.3rem;
  margin-bottom: 1.3rem;
}
.is-style-wlt-custom-button a:hover,
.editor-styles-wrapper .is-style-wlt-custom-button a:hover {
  background-color: white !important;
  border: #2D4190 solid 1px !important;
  color: #2D4190 !important;
}

.is-style-wlt-custom-external-file-link a,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a {
  background-color: #e2e6f8;
  padding: 1rem;
  margin-bottom: 4px;
  color: #424242;
  font-weight: 600;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
}
.is-style-wlt-custom-external-file-link a:hover,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a:hover {
  background-color: #2D4190;
  color: white;
}
.is-style-wlt-custom-external-file-link a:hover::before,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a:hover::before {
  color: white;
}
.is-style-wlt-custom-external-file-link a::before,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a::before {
  font: var(--fa-font-solid);
  content: "\f15b";
  font-size: 1rem;
  color: #424242;
  padding-right: 1rem;
}

.aioseo-html-page-sitemap a {
  text-decoration: none;
}